<template>
  <!---->
  <div>
    <!---->
    <el-select v-model="itemSelect" style="width: 120px">
      <el-option v-for="item in searchCondition"
                 :key="item.value" :label="item.label"
                 :value="item.value">
      </el-option>
    </el-select>
    <el-input placeholder="" style="width: 180px"
              v-model="searchKeys" clearable/>
    <el-button icon="el-icon-search" plain @click="search"></el-button>
    <el-button icon="el-icon-refresh" plain @click="reset"></el-button>
    <!-- max-heightheight -->
    <el-table :data="tableData" stripe border max-height="500" height="450"
              v-loading="loading"
              element-loading-text=""
              element-loading-spinner="el-icon-loading"
              element-loading-background="rgba(0, 0, 0, 0.8">
      <el-table-column label="" type="index" width="50px" align="center"/>
      <el-table-column prop="name" label="" align="center"/>
      <el-table-column prop="grade" label="" align="center"/>
      <el-table-column prop="number" label="" align="center"/>
      <el-table-column prop="department" label="" align="center"/>
      <el-table-column prop="major" label="" align="center"/>
      <el-table-column type="expand" align="center" label="" width="50px">
        <template slot-scope="props">
          <el-form label-position="left" inline class="demo-table-expand">
            <el-form-item label=":">
              <span>{{ props.row.name }}</span>
            </el-form-item>
            <el-form-item label=":">
              <span>{{ props.row.grade}}</span>
            </el-form-item>
            <el-form-item label=":">
              <span>{{ props.row.number }}</span>
            </el-form-item>
            <el-form-item label=":">
              <span v-if="props.row.sex==='0'" style="color: blue">
                  
              </span>
              <span v-else style="color: red">
                  
              </span>
            </el-form-item>
            <el-form-item label=":">
              <span>{{ props.row.department }}</span>
            </el-form-item>
            <el-form-item label=":">
              <span>{{ props.row.major }}</span>
            </el-form-item>
            <el-form-item label="  :">
              <span>{{ props.row.classTeacher}}</span>
            </el-form-item>
          </el-form>
        </template>
      </el-table-column>
    </el-table>
    <el-pagination background
                   @size-change="handleSizeChange"
                   @current-change="handleCurrentChange"
                   :current-page="currentPage" :page-sizes="counts"
                   :page-size="count"
                   layout="total, sizes, prev, pager, next, jumper"
                   :total="totalCount">
    </el-pagination>
  </div>
</template>

<script>
  export default {
    data () {
      return {
        tableData: [],
        //
        currentPage: 1,
        //
        totalPage: 1,
        counts:
          [100, 200, 300, 400, 500],
        //
        count: 100,
        //
        totalCount: 1,
        //
        itemSelect: 'name',
        searchKeys: '',
        searchCondition: [
          {value: 'name', label: ''},
          {value: 'number', label: ''},
          {value: 'grade', label: ''},
          {value: 'department', label: ''},
          {value: 'major', label: ''},
        ],
        loading: true
      }
    },
    methods: {
      handleSizeChange (val) {
        this.count = val
        this.search()
      },
      //
      handleCurrentChange (val) {
        //
        this.currentPage = val
        this.search()
      },
      //
      search () {
        const vm = this
        let item = this.itemSelect
        let key = this.searchKeys.trim()
        let condition = {
          currentPage: this.currentPage,
          count: vm.count,
          userId: sessionStorage.getItem('id')
        }
        if (item === 'name') {
          condition.name = key
        } else if (item === 'number') {
          condition.number = key
        } else if (item === 'grade') {
          condition.grade = key
        }
        else if (item === 'department') {
          condition.department = key
        } else {
          condition.major = key
        }
        //
        let url = this.url_request.ip_port_dev + '/student_check'
        vm.netWorkRequest('post', url, condition, function (response) {
          //
          let pageInfo = response.pageInfo
          vm.totalPage = pageInfo.totalPage
          vm.totalCount = pageInfo.totalCount
          //
          vm.tableData = response.data
          vm.loading = false
        })
      },
      //
      reset () {
        this.itemSelect = 'name'
        this.searchKeys = ''
        this.loading = true
        let url = this.url_request.ip_port_dev + '/student_check'
        const vm = this
        vm.netWorkRequest('post', url, {
          userId: sessionStorage.getItem('id'),
          currentPage: 1,
          count: 100
        }, function (response) {
          //
          let pageInfo = response.pageInfo
          vm.totalPage = pageInfo.totalPage
          vm.totalCount = pageInfo.totalCount
          //
          vm.tableData = response.data
          vm.loading = false
        })
      }
    },
    //
    mounted () {
      this.reset()
    }
  }
</script>

<style scoped>
  .demo-table-expand {
    font-size: 0;

  }

  .demo-table-expand label {
    width: 90px;
    color: #99a9bf;
  }

  .demo-table-expand .el-form-item {
    margin-right: 0;
    margin-bottom: 0;
    width: 50%;
  }
</style>



