/**
 * js,
 *
 */
export default {
  install: function (Vue, options) {
    /**
     * 
     * @param way  get/post
     * @param url 
     * @param params 
     * @param fn 
     */
    Vue.prototype.netWorkRequest = function (way, url, params, fn) {
      if (way === 'get') {
        this.axios.get(url, {
          params: params,
        }).then(response => {
          fn(response.data)
        }).catch(error => {
          this.$message({
            title: '',
            message: ',',
            type: 'error',
            center: true
          })
        }).finally(() => {
          return false
        })
      } else {
        this.axios({
          method: 'post',
          url: url,
          data: params
        }).then(response => {
          fn(response.data)
        }).catch(error => {
          this.$message({
            title: '',
            message: error,
            type: 'error',
            center: true
          })
        })
      }
    }
    /**
     * 
     * @param title 
     * @param message 
     * @param fn 
     */
    Vue.prototype.myConfirm = function (title, message, fn) {
      if (title === null) {
        title = ''
      }
      if (message === null) {
        message = '?!'
      }
      this.$confirm(message, title, {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      }).then(() => {
        fn()
      }).catch(() => {
        this.$message({
          type: 'info',
          message: ''
        })
      })
    }
    /**
     * 
     * @param title  
     * @param toast 
     * @param fn    
     */
    Vue.prototype.myPrompt = function (title, toast, fn) {
      this.$prompt(toast, title, {
        confirmButtonText: '',
        cancelButtonText: '',
        // inputPattern: /[\w!#$%&'*+/=?^_`{|}~-]+(?:\.[\w!#$%&'*+/=?^_`{|}~-]+)*@(?:[\w](?:[\w-]*[\w])?\.)+[\w](?:[\w-]*[\w])?/,
        // inputErrorMessage: ''
      }).then(({value}) => {
        fn(value)
      }).catch(() => {
        this.$message({
          type: 'info',
          message: ''
        })
      })
    }
  }
}



