<template>
  <el-form :model="form" :rules="rules" ref="login_form" label-width="100px"
           class="login-form">
    <p class="title"></p>
    <el-form-item label=":" prop="oldPass">
      <el-input v-model="form.oldPass" show-password
                placeholder=""></el-input>
    </el-form-item>
    <el-form-item label=":" prop="newPass">
      <el-input v-model="form.newPass" show-password
                placeholder=""></el-input>
    </el-form-item>
    <el-form-item label=":" prop="againPass">
      <el-input v-model="form.againPass" show-password
                placeholder=""></el-input>
    </el-form-item>
    <el-form-item>
      <el-button type="primary" @click="alter"></el-button>
      <el-button @click="reset"></el-button>
    </el-form-item>
  </el-form>
</template>

<script>
  export default {
    name: 'AlterPassWord',
    data () {
      return {
        form: {
          oldPass: '',
          againPass: '',
          newPass: '',
        },
        rules: {
          oldPass: [
            {required: true, message: '', trigger: 'blur'},
            {min: 6, max: 20, message: '6-20', trigger: 'blur'}
          ],
          newPass: [
            {required: true, message: '', trigger: 'blur'},
            {min: 6, max: 20, message: '6-20', trigger: 'blur'}
          ],
          againPass: [
            {required: true, message: '', trigger: 'blur'},
            {min: 6, max: 20, message: '6-20', trigger: 'blur'}
          ]
        }
      }
    },
    methods: {
      alter () {
        let vm = this
        const oldPass = this.form.oldPass
        const newPass = this.form.newPass
        const againPass = this.form.againPass
        this.$refs.login_form.validate((valid) => {
          if (valid) {
            if (newPass !== againPass) {
              this.$message.error(',')
            } else {
              const url = vm.url_request.ip_port_dev + '/alter_password'
              //
              vm.netWorkRequest('post', url, {
                username: sessionStorage.getItem('username'),
                password: oldPass,
                newPassWord: newPass
              }, function (response) {
                let type = 'success'
                if (response === '') {
                  type = 'error'
                }
                vm.$message({
                  type: type,
                  message: response
                })
              })
            }
          }
        })
      },
      reset () {
        this.$refs.login_form.resetFields()
      }
    }
  }
</script>

<style scoped>
  .login-form {
    width: 40%;
    text-align: center;
    margin: 50px auto;
  }

  .el-input {
    width: 80%;
  }

  .title {
    color: white;
    font-size: 30px;
  }
</style>



