<template>
  <!---->
  <div>
    <!---->
    <el-select v-model="itemSelect" style="width: 120px">
      <el-option v-for="item in searchCondition" :key="item.value" :label="item.label"
                 :value="item.value">
      </el-option>
    </el-select>
    <el-input placeholder="" style="width: 200px"
              v-model="searchKeys" clearable/>
    <el-button icon="el-icon-search" plain @click="search"></el-button>
    <el-button icon="el-icon-refresh" plain @click="reset"></el-button>
    <!-- max-heightheight -->
    <el-table :data="tableData" stripe border max-height="500" height="450"
              v-loading="loading"
              element-loading-text=""
              element-loading-spinner="el-icon-loading"
              element-loading-background="rgba(0, 0, 0, 0.8"
              @selection-change="selectionChange"
              ref="table">
      <el-table-column type="selection"/>
      <el-table-column label="" align="center" type="index" width="50"/>
      <el-table-column prop="name" align="center" label=""/>
      <el-table-column prop="grade" align="center" label=""/>
      <el-table-column prop="number" align="center" label=""/>
      <el-table-column prop="department" align="center" label=""/>
      <el-table-column prop="major" align="center" label=""/>
      <el-table-column label="" width="160" align="center">
        <template slot-scope="scope">
          <el-button class="el-icon-check" size="mini" @click="on(scope.$index,scope.row)">
          </el-button>
          <el-button class="el-icon-close" size="mini" type="danger" @click="off(scope.$index,scope.row)">
          </el-button>
        </template>
      </el-table-column>
    </el-table>
    <div class="footer">
      <el-button plain class="el-icon-upload" @click="submit">
      </el-button>
      <el-button class="el-icon-refresh-left" plain @click="resetTable"></el-button>
    </div>
    <el-pagination background
                   @size-change="handleSizeChange"
                   @current-change="handleCurrentChange"
                   :current-page="currentPage" :page-sizes="counts"
                   :page-size="count"
                   layout="total, sizes, prev, pager, next, jumper"
                   :total="totalCount">
    </el-pagination>
  </div>
</template>

<script>
  export default {
    name: 'OnlineCheckName',
    data () {
      return {
        reason: '',
        //id
        selections: [],
        dialogVisible: false,
        tableData: [],
        //
        currentPage: 1,
        //
        totalPage: 1,
        counts:
          [100, 200, 300, 400, 500],
        //
        count: 100,
        //
        totalCount: 1,
        //
        itemSelect: 'name',
        searchKeys: '',
        searchCondition: [
          {value: 'name', label: ''},
          {value: 'number', label: ''},
          {value: 'grade', label: ''},
          {value: 'department', label: ''},
          {value: 'major', label: ''},
        ],
        loading: true
      }
    },
    methods: {
      handleSizeChange (val) {
        this.count = val
        this.search()
      },
      //
      handleCurrentChange (val) {
        this.currentPage = val
        this.search()
      },
      //
      search () {
        const vm = this
        let item = this.itemSelect
        let key = this.searchKeys.trim()
        let condition = {
          currentPage: vm.currentPage,
          count: vm.count,
          userId: sessionStorage.getItem('id')
        }
        if (item === 'name') {
          condition.name = key
        } else if (item === 'number') {
          condition.number = key
        } else if (item === 'grade') {
          condition.grade = key
        } else if (item === 'department') {
          condition.department = key
        } else {
          condition.major = key
        }
        //
        let url = this.url_request.ip_port_dev + '/student_check'
        vm.netWorkRequest('post', url, condition, function (response) {
          let pageInfo = response.pageInfo
          vm.totalPage = pageInfo.totalPage
          vm.totalCount = pageInfo.totalCount
          vm.tableData = response.data
          vm.loading = false
        })
      },
      //
      reset () {
        this.itemSelect = 'name'
        this.searchKeys = ''
        this.loading = true
        let url = this.url_request.ip_port_dev + '/student_check'
        const vm = this
        vm.netWorkRequest('post', url, {
          userId: sessionStorage.getItem('id'),
          currentPage: 1,
          count: 100
        }, function (response) {
          let pageInfo = response.pageInfo
          vm.totalPage = pageInfo.totalPage
          vm.totalCount = pageInfo.totalCount
          vm.tableData = response.data
          vm.loading = false
        })
      },
      //
      selectionChange (selection) {
        this.selections = selection
      },
      //
      on (index, row) {
        this.$refs.table.toggleRowSelection(row, false)
      },
      off (index, row) {
        this.$refs.table.toggleRowSelection(row, true)
      },
      submit () {
        const userId = sessionStorage.getItem('id')
        const vm = this
        //()
        vm.selections.forEach((item, index) => {
          vm.selections[index].online = 1
          vm.selections[index].userId = userId
        })
        let url = this.url_request.ip_port_dev + '/online_checkName'
        vm.netWorkRequest('post', url, vm.selections, function (response) {
          vm.$message({
            type: 'success',
            message: response
          })
        })
      },
      resetTable () {
        this.$refs.table.clearSelection()
      }
    },
    //
    mounted () {
      this.reset()
    }
  }
</script>

<style scoped>
  .footer {
    margin-top: 10px;
    text-align: center;
  }
</style>



