<template>
  <!--
  
  
  -->
  <div>
    <el-button plain @click="showDialog(0)">
      <i class="el-icon-plus"></i>
    </el-button>
    <span class="taskName">:</span>
    <el-input placeholder="" style="width: 180px"
              v-model="taskName" clearable/>
    <el-button icon="el-icon-search" plain @click="searchTask"></el-button>
    <el-button icon="el-icon-refresh" plain @click="resetTask"></el-button>
    <el-table :data="tableData" stripe border max-height="500" height="450"
              v-loading="loading" element-loading-text=""
              element-loading-spinner="el-icon-loading"
              element-loading-background="rgba(0, 0, 0, 0.8">
      <el-table-column label="" type="index" align="center" width="50"/>
      <el-table-column align="center" prop="name" label=""/>
      <el-table-column align="center" prop="describes" label=""/>
      <el-table-column align="center" prop="createDate" label=""/>
      <el-table-column type="expand" label="" width="50px">
        <template slot-scope="props">
          :
          <el-button circle icon="el-icon-link" @click="download(props.row.path)">
          </el-button>
        </template>
      </el-table-column>
      <el-table-column label="" align="center">
        <template slot-scope="scope">
          <el-button size="mini" @click="column_edit(scope.$index)">
            <i class="el-icon-edit-outline"></i>
          </el-button>
          <el-button size="mini" type="danger" @click="column_delete(scope.$index)">
            <i class="el-icon-delete-solid"></i>
          </el-button>
        </template>
      </el-table-column>
    </el-table>
    <el-pagination background
                   @size-change="handleSizeChange"
                   @current-change="handleCurrentChange"
                   :current-page="currentPage" :page-sizes="counts"
                   :page-size="count"
                   layout="total, sizes, prev, pager, next, jumper"
                   :total="totalCount">
    </el-pagination>
    <!---->
    <el-dialog :title="title" :visible.sync="dialogFormVisible"
               :center="true" :close-on-click-modal="false">
      <el-form :model="form" :rules="rules" ref="dialog_form" label-width="100px">
        <el-form-item label=":" prop="name">
          <el-input placeholder="" v-model="form.name"/>
        </el-form-item>
        <el-form-item label=":" prop="describes">
          <el-input placeholder="" v-model="form.describes"/>
        </el-form-item>
        <el-form-item label=":" prop="file">
          <el-upload
            class="upload-demo" ref="upload" :action="url"
            :file-list="form.file" :limit="1" :data="form" :auto-upload="false"
            :on-success="success" :on-error="error">
            <el-button slot="trigger" class="el-icon-upload" size="small" plain></el-button>
          </el-upload>
          <span class="toast">10MB </span>
        </el-form-item>
        <span v-if="title===''">
              <p style="margin-left: 30px ;color: #00FF7F;font-weight: bold;">:</p>
          <!---->
          <el-select v-model="itemSelect" style="width: 120px">
            <el-option v-for="item in searchCondition" :key="item.value" :label="item.label"
                       :value="item.value">
            </el-option>
          </el-select>
          <el-input placeholder="" style="width: 180px"
                    v-model="searchKeys" clearable/>
          <el-button icon="el-icon-search" plain @click="searchInfo"></el-button>
          <el-button icon="el-icon-refresh" plain @click="reset"></el-button>
          <!-- max-heightheight -->
          <el-table :data="innerTableData" stripe border max-height="300" height="280"
                    v-loading="loading"
                    element-loading-text=""
                    element-loading-spinner="el-icon-loading"
                    element-loading-background="rgba(0, 0, 0, 0.8"
                    @select="selectionSingle"
                    @select-all="selectAll">
            <el-table-column type="selection"/>
            <el-table-column label="" type="index" align="center" width="50"/>
            <el-table-column prop="name" label="" align="center"/>
            <el-table-column prop="number" label="" align="center"/>
          </el-table>
          <el-pagination background
                         @size-change="innerHandleSizeChange"
                         @current-change="innerHandleCurrentChange"
                         :current-page="innerCurrentPage"
                         :page-sizes="innerCounts"
                         :page-size="innerCount"
                         layout="total, sizes, prev, pager, next, jumper"
                         :total="innerTotalCount">
          </el-pagination>
        </span>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="dialogFormVisible = false"> </el-button>
        <el-button type="primary" @click="enSure"> </el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
  export default {
    name: 'TaskManage',
    data () {
      return {
        //
        tableData: [],
        //
        innerTableData: [],
        //
        currentPage: 1,
        innerCurrentPage: 1,
        //
        totalPage: 1,
        innerTotalPage: 1,
        //
        counts: [100, 200, 300, 400, 500],
        innerCounts: [100, 200, 300, 400, 500],
        //
        count: 100,
        innerCount: 100,
        //
        totalCount: 1,
        innerTotalCount: 1,
        //
        itemSelect: 'name',
        searchKeys: null,
        //
        taskName: null,
        //id
        selections: [],
        searchCondition: [
          {value: 'name', label: ''},
          {value: 'number', label: ''},
          {value: 'grade', label: ''},
          {value: 'department', label: ''},
          {value: 'major', label: ''},
        ],
        loading: true,
        url: this.url_request.ip_port_dev + '/issue_task',
        dialogFormVisible: false,
        form: {
          id: -1,
          name: '',
          describes: '',
          file: [],
          //
          studentIds: [],
          userId: sessionStorage.getItem('id')
        },
        title: '',
        rules: {
          name: [
            {required: true, message: '', trigger: 'blur'},
            {min: 6, max: 20, message: '6-20', trigger: 'blur'}
          ],
          describes: [
            {required: true, message: '', trigger: 'blur'},
            {min: 3, max: 100, message: '3-100', trigger: 'blur'}
          ]
        }
      }
    },
    methods: {
      //
      handleSizeChange (val) {
        this.count = val
        this.searchInfo()
      },
      //
      handleCurrentChange (val) {
        // 
        this.innerCurrentPage = val
        this.searchInfo()
      },
      innerHandleSizeChange (val) {
        this.innerCount = val
        this.searchInfo()
      },
      innerHandleCurrentChange (val) {
        // 
        this.innerCurrentPage = val
        this.searchInfo()
      },
      //
      initStudentInfo () {
        const vm = this
        this.itemSelect = 'name'
        this.searchKeys = ''
        let url = this.url_request.ip_port_dev + '/student_check'
        vm.netWorkRequest('post', url, {
          userId: sessionStorage.getItem('id'),
          currentPage: 1,
          count: 100
        }, function (response) {
          //
          let pageInfo = response.pageInfo
          vm.innerTotalPage = pageInfo.totalPage
          vm.innerTotalCount = pageInfo.totalCount
          //
          vm.innerTableData = response.data
          vm.loading = false
        })
      },
      //
      searchInfo () {
        let item = this.itemSelect
        let key = this.searchKeys.trim()
        const vm = this
        let condition = {
          currentPage: this.innerCurrentPage,
          count: this.innerCount,
          userId: sessionStorage.getItem('id')
        }
        if (item === 'name') {
          condition.name = key
        } else if (item === 'number') {
          condition.number = key
        } else if (item === 'grade') {
          condition.grade = key
        }
        else if (item === 'department') {
          condition.department = key
        } else {
          condition.major = key
        }
        //
        let url = this.url_request.ip_port_dev + '/student_check'
        vm.netWorkRequest('post', url, condition, function (response) {
          //
          let pageInfo = response.pageInfo
          vm.innerTotalPage = pageInfo.totalPage
          vm.innerTotalCount = pageInfo.totalCount
          //
          vm.innerTableData = response.data
          vm.loading = false
        })
      },
      //
      reset () {
        this.innerCurrentPage = 1
        const vm = this
        this.itemSelect = 'name'
        this.searchKeys = ''
        let url = this.url_request.ip_port_dev + '/student_check'
        vm.netWorkRequest('post', url, {
          userId: sessionStorage.getItem('id'),
          currentPage: 1,
          count: 100
        }, function (response) {
          //
          let pageInfo = response.pageInfo
          vm.innerTotalPage = pageInfo.totalPage
          vm.innerTotalCount = pageInfo.totalCount
          //
          vm.innerTableData = response.data
          vm.loading = false
        })
      },
      //selection
      //selection
      selectionSingle (selection, row) {
        this.selections = selection
      },
      //
      selectAll (selection) {
        this.selections = selection
      },
      /**
       * 
       * @param what 
       * @param index ,
       */
      showDialog (what, index) {
        //bug----
        this.$nextTick(() => {
          this.$refs.dialog_form.resetFields()
        })
        if (what === 0) {
          //
          this.initStudentInfo()
          //id-1,id
          this.form.id = -1
          this.form.studentIds = []
          this.dialogFormVisible = true
          this.title = ''
        } else {
          this.title = ''
          this.dialogFormVisible = true
          const object = Object.assign({}, this.tableData[index])
          this.form = object
        }
      },
      //
      enSure () {
        const vm = this
        //
        this.$refs.dialog_form.validate((validate) => {
          if (validate) {
            //id
            vm.selections.forEach(item => {
              vm.form.studentIds.push(item.id)
            })
            this.$refs.upload.submit()
          } else {
            return false
          }
        })
      },
      //
      success (response, file, fileList) {
        if (response === '') {
          this.$message({
            message: '',
            type: 'success'
          })
        } else {
          this.$message({
            message: '',
            type: 'error'
          })
        }
        this.form.name = ''
        this.form.describes = ''
        this.form.file = []
        this.dialogFormVisible = false
        this.resetTask()
      },
      //
      error (error, file, fileList) {
        this.$message({
          message: '',
          type: 'error'
        })
      },
      search () {
        const vm = this
        let item = this.itemSelect
        let key = this.searchKeys.trim()
        let condition = {
          userId: sessionStorage.getItem('id'),
          currentPage: this.currentPage,
          count: this.count
        }
        if (item === 'name') {
          condition.name = key
        } else if (item === 'number') {
          condition.number = key
        } else if (item === 'department') {
          condition.department = key
        } else {
          condition.major = key
        }
        //
        let url = this.url_request.ip_port_dev + '/student_check'
        vm.netWorkRequest('post', url, condition, function (response) {
          //
          let pageInfo = response.data.pageInfo
          vm.totalPage = pageInfo.totalPage
          vm.totalCount = pageInfo.totalCount
          //
          vm.tableData = response.data.data
          vm.loading = false
        })
      },
      //
      column_edit (index) {
        this.showDialog(1, index)
      },
      //
      column_delete (index) {
        let id = this.tableData[index].id
        const vm = this
        let url = this.url_request.ip_port_dev + '/issue_task_delete'
        vm.myConfirm(null, null, function () {
          vm.netWorkRequest('get', url, {
            id: id
          }, function (response) {
            vm.resetTask()
            vm.$message({
              type: 'success',
              message: response
            })
          })
        })
      },
      download (path) {
        let url = this.url_request.ip_port_dev + '/issue_task_download/'
        window.location.href = url + '?path=' + path
      },
      resetTask () {
        const vm = this
        this.taskName = ''
        this.loading = true
        let url = this.url_request.ip_port_dev + '/issue_task_check'
        vm.currentPage = 1
        vm.netWorkRequest('post', url, {
          userId: sessionStorage.getItem('id'),
          currentPage: 1,
          count: 100
        }, function (response) {
          //
          let pageInfo = response.pageInfo
          vm.totalPage = pageInfo.totalPage
          vm.totalCount = pageInfo.totalCount
          //
          vm.tableData = response.data
        })
        vm.loading = false
      },
      //
      searchTask () {
        const vm = this
        vm.currentPage = 1
        let url = this.url_request.ip_port_dev + '/issue_task_check'
        vm.netWorkRequest('post', url, {
          userId: sessionStorage.getItem('id'),
          name: vm.taskName,
          currentPage: 1,
          count: 100
        }, function (response) {
          //
          let pageInfo = response.pageInfo
          vm.totalPage = pageInfo.totalPage
          vm.totalCount = pageInfo.totalCount
          //
          vm.tableData = response.data
        })
        vm.loading = false
      }
    },
    mounted () {
      this.resetTask()
    }
  }
</script>

<style scoped>
  .toast {
    color: yellow;
    font-weight: bold;
    font-size: 20px;
  }

  .taskName {
    color: white;
    font-weight: bold;
  }
</style>



