<template>
  <!---->
  <div>
    <!---->
    <span class="name">:</span>
    <el-select v-model="courseValue" placeholder=""
               @change="courseSelectChange">
      <el-option
        v-for="item in courses"
        :key="item.value"
        :label="item.label"
        :value="item.value">
      </el-option>
    </el-select>
    <span class="name">:</span>
    <el-select :disabled="examinationSelect" v-model="examinationValue"
               placeholder="" @change="examinationSelectChange">
      <el-option
        v-for="item in examination"
        :key="item.value"
        :label="item.label"
        :value="item.value">
      </el-option>
    </el-select>
    <el-select :disabled="recordSelect" v-model="recordValue"
               placeholder="" @change="recordSelectChange">
      <el-option
        v-for="item in record"
        :key="item.value"
        :label="item.label"
        :value="item.value">
      </el-option>
    </el-select>
    <!-- max-heightheight -->
    <el-table :data="tableData" stripe border max-height="500" height="450"
              v-loading="loading"
              element-loading-text=""
              element-loading-spinner="el-icon-loading"
              element-loading-background="rgba(0, 0, 0, 0.8">
      <el-table-column label="" type="index" width="50px" align="center"/>
      <el-table-column prop="question" label="" align="center"/>
      <el-table-column type="expand" align="center" label="" width="50px">
        <template slot-scope="props">
          <el-form label-position="left" class="demo-table-expand">
            <el-form-item label=":">
              <span>{{ props.row.question }}</span>
            </el-form-item>
            <span v-if="recordValue===1||recordValue===2">
               <el-form-item label="A:">
              <span>{{ props.row.selectA }}</span>
            </el-form-item>
            <el-form-item label="B:">
              <span>{{ props.row.selectB }}</span>
            </el-form-item>
            <el-form-item label="C:">
              <span>{{ props.row.selectC }}</span>
            </el-form-item>
            <el-form-item label="D:">
              <span>{{ props.row.selectD }}</span>
            </el-form-item>
            </span>
            <span v-if="recordValue===2">
               <el-form-item label="E:">
              <span>{{ props.row.selectE }}</span>
            </el-form-item>
            <el-form-item label="F:">
              <span>{{ props.row.selectF }}</span>
            </el-form-item>
            <el-form-item label="G:">
              <span>{{ props.row.selectG }}</span>
            </el-form-item>
            </span>
            <el-form-item label=":">
              <span>{{ props.row.correct }}</span>
            </el-form-item>
            <el-form-item label=":">
              <span>{{ props.row.analyze }}</span>
            </el-form-item>
          </el-form>
        </template>
      </el-table-column>
    </el-table>
    <el-pagination background
                   @size-change="handleSizeChange"
                   @current-change="handleCurrentChange"
                   :current-page="currentPage" :page-sizes="counts"
                   :page-size="count"
                   layout="total, sizes, prev, pager, next, jumper"
                   :total="totalCount">
    </el-pagination>
  </div>
</template>

<script>
  export default {
    name: 'OnlineExamination',
    data () {
      return {
        courses: [],
        courseValue: null,
        examination: [],
        examinationValue: null,
        examinationSelect: true,
        recordSelect: true,
        recordValue: null,
        record: [
          {label: '', value: 1},
          {label: '', value: 2},
          {label: '', value: 3}
        ],
        tableData: [],
        //
        currentPage: 1,
        //
        totalPage: 1,
        counts:
          [100, 200, 300, 400, 500],
        //
        count: 100,
        //
        totalCount: 1,
        //
        itemSelect: 'name',
        searchKeys: '',
        searchCondition: [
          {value: 'name', label: ''},
          {value: 'number', label: ''},
          {value: 'department', label: ''},
          {value: 'major', label: ''},
        ],
        loading: true
      }
    },
    methods: {
      handleSizeChange (val) {
        this.count = val
        this.search()
      },
      handleCurrentChange (val) {
        //
        this.currentPage = val
        this.search()
      },
      search () {
        const vm = this
        const url = vm.url_request.ip_port_dev + '/public_data_examination_record_query'
        vm.netWorkRequest('post', url, {
          currentPage: vm.currentPage,
          count: vm.count,
          questionType: vm.recordValue,
          examinationId: vm.examinationValue
        }, function (response) {
          //
          let pageInfo = response.pageInfo
          vm.totalPage = pageInfo.totalPage
          vm.totalCount = pageInfo.totalCount
          //
          vm.tableData = response.data
          vm.loading = false
        })
      },
      //
      courseSelectChange (value) {
        this.courseValue = value
        //
        const vm = this
        const url = vm.url_request.ip_port_dev + '/public_data_examination_query'
        vm.netWorkRequest('post', url, {value: value}, function (response) {
          if (response.length > 0) {
            vm.examination = response
            vm.examinationSelect = false
          } else {
            vm.examinationSelect = true
            vm.recordSelect = true
            vm.examinationValue = ''
            vm.recordValue = ''
            vm.tableData = []
          }
        })
      },
      //
      examinationSelectChange (value) {
        this.examinationValue = value
        this.recordSelect = false
      },
      //,
      recordSelectChange (value) {
        const vm = this
        vm.recordValue = value
        const url = vm.url_request.ip_port_dev + '/public_data_examination_record_query'
        vm.netWorkRequest('post', url, {
          currentPage: 1,
          count: 100,
          questionType: value,
          examinationId: vm.examinationValue
        }, function (response) {
          //
          let pageInfo = response.pageInfo
          vm.totalPage = pageInfo.totalPage
          vm.totalCount = pageInfo.totalCount
          //
          vm.tableData = response.data
          vm.loading = false
        })
      }
    },
    mounted () {
      const vm = this
      let url = vm.url_request.ip_port_dev + '/public_data_course_query'
      vm.netWorkRequest('post', url, {
        currentPage: 1,
        count: 500,
      }, function (response) {
        vm.courses = response.data
      })
    }
  }
</script>

<style scoped>
  .name {
    color: white;
    font-size: 25px;
  }
</style>



