<template>
  <!---->
  <div>
    <!---->
    <el-select v-model="itemSelect" style="width: 120px">
      <el-option v-for="item in searchCondition" :key="item.value" :label="item.label"
                 :value="item.value">
      </el-option>
    </el-select>
    <el-input placeholder="" style="width: 200px"
              v-model="searchKeys" clearable/>
    <el-button icon="el-icon-search" plain @click="search"></el-button>
    <el-button icon="el-icon-refresh" plain @click="reset"></el-button>
    <!-- max-heightheight -->
    <el-table :data="tableData" stripe border max-height="500" height="450"
              v-loading="loading"
              element-loading-text=""
              element-loading-spinner="el-icon-loading"
              element-loading-background="rgba(0, 0, 0, 0.8"
              ref="table">
      <el-table-column label="" align="center" type="index" width="50"/>
      <el-table-column prop="name" align="center" label=""/>
      <el-table-column width="150" label="" align="center">
        <template slot-scope="scope">
          <el-button size="mini" @click="detail(scope.$index, scope.row)">
            <i class="el-icon-zoom-in"></i>
          </el-button>
        </template>
      </el-table-column>
    </el-table>
    <el-pagination background
                   @size-change="handleSizeChange"
                   @current-change="handleCurrentChange"
                   :current-page="currentPage" :page-sizes="counts"
                   :page-size="count"
                   layout="total, sizes, prev, pager, next, jumper"
                   :total="totalCount">
    </el-pagination>
    <!---->
    <el-dialog title="" :visible.sync="dialogVisible">
      <el-table :data="onlineData">
        <el-table-column label="" :index="indexMethod" type="index" width="50"/>
        <el-table-column property="createDate" label=""/>
        <el-table-column property="online" label="" width="200"/>
      </el-table>
    </el-dialog>
  </div>
</template>

<script>
  export default {
    name: 'OnlineCheckNameCheck',
    data () {
      return {
        dialogVisible: false,
        tableData: [],
        onlineData: [],
        //
        currentPage: 1,
        //
        totalPage: 1,
        counts:
          [100, 200, 300, 400, 500],
        //
        count: 100,
        //
        totalCount: 1,
        //
        itemSelect: 'name',
        searchKeys: '',
        searchCondition: [
          {value: 'name', label: ''},
          {value: 'number', label: ''},
          {value: 'grade', label: ''},
          {value: 'department', label: ''},
          {value: 'major', label: ''},
        ],
        loading: true
      }
    },
    methods: {
      handleSizeChange (val) {
        this.count = val
        this.search()
      },
      handleCurrentChange (val) {
        this.currentPage = val
        this.search()
      },
      //
      search () {
        const vm = this
        let item = this.itemSelect
        let key = this.searchKeys.trim()
        let condition = {
          currentPage: this.currentPage,
          count: vm.count,
          userId: sessionStorage.getItem('id')
        }
        if (item === 'name') {
          condition.name = key
        } else if (item === 'number') {
          condition.number = key
        } else if (item === 'grade') {
          condition.grade = key
        } else if (item === 'department') {
          condition.department = key
        } else {
          condition.major = key
        }
        //
        let url = this.url_request.ip_port_dev + '/student_check'
        vm.netWorkRequest('post', url, condition, function (response) {
          let pageInfo = response.pageInfo
          vm.totalPage = pageInfo.totalPage
          vm.totalCount = pageInfo.totalCount
          vm.tableData = response.data
          vm.loading = false
        })
      },
      //
      reset () {
        this.itemSelect = 'name'
        this.searchKeys = ''
        this.loading = true
        let url = this.url_request.ip_port_dev + '/student_check'
        const vm = this
        vm.netWorkRequest('post', url, {
          userId: sessionStorage.getItem('id'),
          currentPage: 1,
          count: 100
        }, function (response) {
          let pageInfo = response.pageInfo
          vm.totalPage = pageInfo.totalPage
          vm.totalCount = pageInfo.totalCount
          vm.tableData = response.data
          vm.loading = false
        })
      },
      //excel
      exportExcel () {
        let ids = ''
        this.selections.forEach(item => {
          ids += item.id + ','
        })
        if (ids.length === 0) {
          this.$message({
            message: '!',
            type: 'warning'
          })
          return
        }
        let url = this.url_request.ip_port_dev + '/student_export'
        //,axios
        window.location.href = url + '?ids=' + ids
      },
      //
      detail (index, row) {
        this.dialogVisible = true
        const vm = this
        let url = this.url_request.ip_port_dev + '/online_check'
        // const vm = this
        this.netWorkRequest('get', url, {
          studentId: row.id,
          userId: sessionStorage.getItem('id')
        }, function (response) {
          vm.onlineData = response
        })
      },
      //
      indexMethod (index) {
        return index + 1
      }
    },
    mounted () {
      this.reset()
    }
  }
</script>

<style scoped>
</style>



