<template>
  <!--
  -->
  <div>
    <div class="header">
      <el-button plain class="el-icon-plus" @click="addCourse"></el-button>
      <el-input @change="change" class="input" v-model="input" placeholder=""></el-input>
    </div>
    <!-- max-heightheight -->
    <el-table :data="tableData" stripe border max-height="500" height="450"
              v-loading="loading" element-loading-text=""
              element-loading-spinner="el-icon-loading"
              element-loading-background="rgba(0, 0, 0, 0.8">
      <el-table-column align="center" label="" type="index" width="50"/>
      <el-table-column align="center" prop="label" sortable label=""/>
      <el-table-column align="center" prop="value" sortable label=""/>
      <el-table-column type="expand" align="center" label="" width="50px">
        <template slot-scope="props">
          <el-form label-position="left" class="demo-table-expand">
            <el-form-item label=":">
              <span>{{ props.row.describes }}</span>
            </el-form-item>
            <el-form-item label=":">
              <span>{{ props.row.createName }}</span>
            </el-form-item>
            <el-form-item label=":">
              <span>{{ props.row.createDate}}</span>
            </el-form-item>
            <el-form-item label=":">
              <span>{{ props.row.updateDate }}</span>
            </el-form-item>
          </el-form>
        </template>
      </el-table-column>
      <el-table-column label="" align="center" width="260px">
        <template slot-scope="scope">
          <el-button size="mini" type="primary" class="el-icon-edit-outline"
                     @click="column_edit(scope.$index)">
          </el-button>
          <el-button class="el-icon-delete-solid" size="mini" type="danger"
                     @click="column_delete(scope.$index)">
          </el-button>
        </template>
      </el-table-column>
    </el-table>
    <el-pagination background
                   @size-change="handleSizeChange"
                   @current-change="handleCurrentChange"
                   :current-page="currentPage" :page-sizes="counts"
                   :page-size="count"
                   layout="total, sizes, prev, pager, next, jumper"
                   :total="totalCount">
    </el-pagination>

    <el-dialog :title="title" :visible.sync="dialogFormVisible"
               :center="true" :close-on-click-modal="false">
      <el-form :model="form" :rules="rules" ref="dialog_form" label-width="100px">
        <el-form-item label=":" prop="label">
          <el-input v-model="form.label" placeholder=""/>
        </el-form-item>
        <el-form-item label=":" prop="value">
          <el-input-number :min="1" :max="999999999" v-model="form.value" placeholder=","/>
        </el-form-item>
        <el-form-item label=":" prop="describes">
          <el-input v-model="form.describes"
                    type="textarea"
                    :autosize="{ minRows: 4, maxRows: 6}"
                    placeholder=""/>
        </el-form-item>

      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="dialogFormVisible = false"> </el-button>
        <el-button type="primary" @click="ok"> </el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
  export default {
    name: 'CourseManage',
    data () {
      return {
        loading: true,
        input: null,
        title: '',
        dialogFormVisible: false,
        form: {
          id: -1,
          label: null,
          value: null,
          describes: null,
          createName: sessionStorage.getItem('username')
        },
        rules: {
          label: [
            {required: true, message: '', trigger: 'blur'},
            {min: 6, max: 20, message: '6-20', trigger: 'blur'}
          ],
          value: [
            {required: true, message: '', trigger: 'blur'}
          ]
        },
        tableData: [],
        //
        currentPage: 1,
        //
        totalPage: 1,
        counts:
          [100, 200, 300, 400, 500],
        //
        count: 100,
        //
        totalCount: 1,
        //
        itemSelect: 'name',
        searchKeys: '',
      }
    },
    methods: {
      //counts
      handleSizeChange (val) {
        this.count = val
        this.search()
      },
      //
      handleCurrentChange (val) {
        //
        this.currentPage = val
        this.search()
      },
      addCourse () {
        this.title = ''
        this.form.id = -1
        this.form.label = ''
        this.form.value = ''
        this.form.describes = ''
        this.dialogFormVisible = true
      },
      search () {

      },
      reset () {
        const vm = this
        let url = vm.url_request.ip_port_dev + '/public_data_course_query'
        vm.netWorkRequest('post', url, {
          currentPage: 1,
          count: 100
        }, function (response) {
          //,json
          vm.tableData = response.data
          //,json
          let pageInfo = response.pageInfo
          //
          vm.totalCount = pageInfo.totalCount
          vm.totalPage = pageInfo.totalPage
          vm.loading = false
        })
      },
      column_edit (index) {
        let obj = Object.assign({}, this.tableData[index])
        this.form = obj
        this.title = ''
        this.dialogFormVisible = true
      },
      column_delete (index) {
        const id = this.tableData[index].id
        const vm = this
        const url = vm.url_request.ip_port_dev + '/course_manage_delete'
        vm.myConfirm(null, null, function () {
          vm.netWorkRequest('post', url, {id: id}, function (response) {
            vm.$message({
              type: 'success',
              message: response
            })
            vm.reset()
          })
        })
      },
      change (value) {
        let condition = {
          currentPage: 1,
          count: 100,
        }
        if (isNaN(value)) {
          condition.label = value
        } else {
          condition.value = value
        }
        const vm = this
        let url = vm.url_request.ip_port_dev + '/public_data_course_query'
        vm.netWorkRequest('post', url, condition, function (response) {
          //,json
          vm.tableData = response.data
          //,json
          let pageInfo = response.pageInfo
          //
          vm.totalCount = pageInfo.totalCount
          vm.totalPage = pageInfo.totalPage
          vm.loading = false
        })
      },
      ok () {
        const vm = this
        let url = vm.url_request.ip_port_dev + '/course_manage_addOrUpdate'
        vm.netWorkRequest('post', url, vm.form, function (response) {
          vm.$message({
            type: 'success',
            message: response
          })
          vm.dialogFormVisible = false
          vm.reset()
        })
      }
    },
    mounted () {
      this.reset()
    }
  }
</script>

<style scoped>
  .header {
    text-align: left;
  }

  .input {
    width: 300px;
  }
</style>



