<template>
  <!--,-->
  <div>
    <!---->
    <el-select v-model="itemSelect" style="width: 120px">
      <el-option v-for="item in searchCondition" :key="item.value" :label="item.label"
                 :value="item.value">
      </el-option>
    </el-select>
    <el-input placeholder="" style="width: 200px"
              v-model="searchKeys" clearable/>
    <el-button icon="el-icon-search" plain @click="search"></el-button>
    <el-button icon="el-icon-refresh" plain @click="reset"></el-button>
    <el-table :data="tableData" stripe border max-height="500" height="450"
              v-loading="loading" element-loading-text=""
              element-loading-spinner="el-icon-loading"
              element-loading-background="rgba(0, 0, 0, 0.8">
      <el-table-column align="center" label="" type="index" width="50"/>
      <el-table-column align="center" prop="user.name" label=""/>
      <el-table-column align="center" prop="homeWork.name" label=""/>
      <el-table-column align="center" prop="submitState" label="">
        <template slot-scope="scope">
        <span style="color: green" v-if="tableData[scope.$index].submitState==='1'">
          
        </span>
          <span v-else style="color: red">
          
        </span>
        </template>
      </el-table-column>
      <el-table-column label="" align="center">
        <template slot-scope="scope">
          <span v-if="tableData[scope.$index].submitState==='1'">
          <el-button size="mini" @click="column_download(scope.$index)">
            <i class="el-icon-download"></i>
          </el-button>
          <el-button size="mini" type="danger" @click="column_read(scope.$index)">
          </el-button>
          </span>
          <span v-else>
            <el-button size="mini" disabled>
               <i class="el-icon-download"></i>
          </el-button>
          <el-button size="mini" type="danger" disabled>
          </el-button>
          </span>
        </template>
      </el-table-column>
    </el-table>
    <el-pagination background
                   @size-change="handleSizeChange"
                   @current-change="handleCurrentChange"
                   :current-page="currentPage" :page-sizes="counts"
                   :page-size="count"
                   layout="total, sizes, prev, pager, next, jumper"
                   :total="totalCount">
    </el-pagination>
    <!---->
    <el-dialog title="" :visible.sync="dialogFormVisible" center>
      <el-form :model="form">
        <el-form-item label=":" :label-width="formLabelWidth">
          {{form.taskName}}
        </el-form-item>
        <el-form-item label=":" :label-width="formLabelWidth">
          {{form.name}}
        </el-form-item>
        <el-form-item label=":" :label-width="formLabelWidth">
          <el-input-number v-model="form.score" :precision="2" :step="0.1" :max="100">
          </el-input-number>
        </el-form-item>
        <el-form-item label=":" :label-width="formLabelWidth">
          <el-input clear type="textarea"
                    :autosize="{ minRows: 2, maxRows: 4}" show-word-limit
                    maxlength="200"
                    v-model="form.remark">
          </el-input>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="dialogFormVisible = false"> </el-button>
        <el-button type="primary" @click="ok"> </el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
  export default {
    name: 'TaskSubmitCheck',
    data () {
      return {
        itemSelect: 'name',
        searchKeys: '',
        searchCondition: [
          {value: 'name', label: ''},
          {value: 'number', label: ''},
          {value: 'grade', label: ''}
        ],
        loading: true,
        tableData: [],
        //
        currentPage: 1,
        //
        totalPage: 1,
        counts:
          [100, 200, 300, 400, 500],
        //
        count: 100,
        //
        totalCount: 1,
        url: this.url_request.ip_port_dev + '/issue_task',
        dialogFormVisible: false,
        formLabelWidth: '150px',
        form: {
          id: -1,
          name: '',//
          taskName: '',//
          score: 0,//
          remark: ''//
        }
      }
    },
    methods: {
      //
      search () {
        const vm = this
        let item = this.itemSelect
        let key = this.searchKeys.trim()
        let condition = {
          currentPage: this.currentPage,
          count: this.count,
          userId: sessionStorage.getItem('id')
        }
        if (item === 'name') {
          condition.name = key
        } else if (item === 'number') {
          condition.number = key
        } else {
          condition.grade = key
        }
        let url = vm.url_request.ip_port_dev + '/student_submit_task_read'
        vm.netWorkRequest('post', url, condition, function (response) {
          //,json
          vm.tableData = response.data
          //,json
          let pageInfo = response.pageInfo
          //
          vm.totalCount = pageInfo.totalCount
          vm.totalPage = pageInfo.totalPage
          vm.loading = false
        })
      },
      reset () {
        const vm = this
        this.currentPage = 1
        this.searchKeys = ''
        const url = vm.url_request.ip_port_dev + '/student_submit_task_read'
        vm.netWorkRequest('post', url, {
          userId: sessionStorage.getItem('id'),
          currentPage: 1,
          count: 100
        }, function (response) {
          //
          let pageInfo = response.pageInfo
          vm.totalPage = pageInfo.totalPage
          vm.totalCount = pageInfo.totalCount
          //
          vm.tableData = response.data
          vm.loading = false
        })
      },
      column_download (index) {
        const path = this.tableData[index].submitPath
        let url = this.url_request.ip_port_dev + '/issue_task_download/'
        window.location.href = url + '?path=' + path
      },
      //
      column_read (index) {
        const vm = this
        //data
        const data = vm.tableData[index]
        vm.form.id = data.id
        vm.form.taskName = data.homeWork.name
        vm.form.name = data.user.name
        vm.form.score = data.score
        vm.form.remark = data.remark
        //
        vm.dialogFormVisible = true
      },
      //
      ok () {
        const vm = this
        const url = vm.url_request.ip_port_dev + '/student_submit_task_correct'
        vm.netWorkRequest('post', url, {
          id: vm.form.id,
          score: vm.form.score,
          remark: vm.form.remark
        }, function (response) {
          vm.$message({
            type: 'success',
            message: response
          })
          vm.dialogFormVisible = false
          vm.reset()
        })
      },
      handleSizeChange (val) {
        this.count = val
        this.search()
      },
      //
      handleCurrentChange (val) {
        //
        this.currentPage = val
        this.search()
      }
    },
    mounted () {
      this.reset()
    }
  }
</script>

<style scoped>
  .table_footer {
    text-align: center;
  }
</style>



