<template>
  <!---->
  <div>
    <el-button plain @click="showDialog(0)">
      <i class="el-icon-circle-plus"></i>
    </el-button>
    <el-select v-model="itemSelect" style="width: 120px">
      <el-option v-for="item in searchCondition" :key="item.value" :label="item.label"
                 :value="item.value">
      </el-option>
    </el-select>
    <el-input placeholder="" style="width: 180px"
              v-model="searchKeys" clearable/>
    <el-button icon="el-icon-search" plain @click="searchInfo"></el-button>
    <el-button icon="el-icon-refresh" plain @click="reset"></el-button>
    <!-- max-heightheight -->
    <el-table :data="tableData" stripe border max-height="500" height="450"
              v-loading="loading" element-loading-text=""
              element-loading-spinner="el-icon-loading"
              element-loading-background="rgba(0, 0, 0, 0.8">
      <el-table-column label="" type="index" width="50px" align="center"/>
      <el-table-column prop="name" label="" align="center"/>
      <el-table-column prop="grade" label="" align="center"/>
      <el-table-column prop="number" label="" align="center"/>
      <el-table-column prop="department" label="" align="center"/>
      <el-table-column prop="major" label="" align="center"/>
      <el-table-column label="" align="center">
        <template slot-scope="scope">
          <el-button size="mini" @click="showDialog(1,scope.$index)">
            <i class="el-icon-edit-outline"></i>
          </el-button>
          <el-button size="mini" type="danger" @click="column_delete(scope.$index)">
            <i class="el-icon-delete-solid"></i>
          </el-button>
        </template>
      </el-table-column>
    </el-table>
    <el-pagination background
                   @size-change="handleSizeChange" @current-change="handleCurrentChange"
                   :current-page="currentPage" :page-sizes="counts"
                   :page-size="count"
                   layout="total, sizes, prev, pager, next, jumper" :total="totalCount">
    </el-pagination>
    <!---->
    <el-dialog :title="title" :visible.sync="dialogFormVisible"
               :center="true" :close-on-click-modal="false" @open="open">
      <el-form :model="form" :rules="rules" :inline="true" ref="dialog_form">
        <el-form-item label=":" prop="name">
          <el-input placeholder="" v-model="form.name"></el-input>
        </el-form-item>
        <el-form-item label=":" prop="sex">
          <el-radio v-model="form.sex" label="0"></el-radio>
          <el-radio v-model="form.sex" label="1"></el-radio>
        </el-form-item>
        <el-form-item label=":" prop="grade">
          <el-input placeholder="" v-model="form.grade"/>
        </el-form-item>
        <el-form-item label=":" prop="number">
          <el-input placeholder="" v-model.number="form.number" @change="numberChange"/>
        </el-form-item>
        <el-form-item label=":" prop="department">
          <el-input placeholder="" v-model="form.department"/>
        </el-form-item>
        <el-form-item label=":" prop="major">
          <el-input placeholder="" v-model="form.major"/>
        </el-form-item>
        <el-form-item label=":" prop="classTeacher">
          <el-input  placeholder="" v-model="form.classTeacher"/>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="dialogFormVisible = false"> </el-button>
        <el-button type="primary" @click="ensure"> </el-button>
      </div>
    </el-dialog>
  </div>
</template>
<script>
  export default {
    name: 'StudentInfoMaintain',
    data () {
      return {
        //
        tableData: [],
        //
        currentPage: 1,
        //
        totalPage: 1,
        counts: [100, 200, 300, 400, 500],
        //
        count: 100,
        //
        totalCount: 1,
        //
        itemSelect: 'name',
        searchKeys: '',
        searchCondition: [
          {value: 'name', label: ''},
          {value: 'number', label: ''},
          {value: 'grade', label: ''},
          {value: 'department', label: ''},
          {value: 'major', label: ''},
        ],
        title: '',
        dialogFormVisible: false,
        form: {
          id: -1,
          name: null,
          sex: '0',
          grade: null,
          number: null,
          department: null,
          major: null,
          classTeacher: null
        },
        loading: true,
        //
        rules: {
          name: [
            {required: true, message: ''},
            {min: 2, max: 6, message: '2-6'}
          ],
          sex: [
            {required: true}
          ],
          grade: [
            {required: true, message: ''},
            {min: 6, max: 20, message: '6-20'}
          ],
          number: [
            {required: true, message: ''},
            {type: 'number', message: ''}
          ],
          department: [
            {required: true, message: ''},
            {min: 3, max: 20, message: '3-20'}
          ],
          major: [
            {required: true, message: ''},
            {min: 3, max: 20, message: '3-20'}
          ]
        }
      }
    },
    methods: {
      /*
      *
      * @param type 
      * @param data 
      * */
      open (type, data) {
        //
        this.$nextTick(() => {
          this.$refs.dialog_form.resetFields()
          switch (type) {//01
            case 0:
              this.form.id = -1
              this.title = ''
              break
            case 1:
              this.title = ''
              this.form = data
              break
            default:
              break
          }
        })
      },
      //
      column_delete (index, row) {
        this.$confirm('?', '', {
          confirmButtonText: '',
          cancelButtonText: ''
        }).then(() => {
          //
          let id = this.tableData[index].id
          let url = this.url_request.ip_port_dev + '/student_manage/delete'
          this.addOrUpdateOrDelete(url, {id: id})
        }).catch(() => {
          this.$message({
            type: 'info',
            message: ''
          })
        })
      },
      /**
       * 
       * @param what 01
       * @param index ,
       */
      showDialog (what, index) {
        this.dialogFormVisible = true
        if (what === 0) {
          this.open(0)
        } else {
          this.title = ''
          //
          let object = Object.assign({}, this.tableData[index])
          this.open(1, object)
        }
      },
      //
      ensure () {
        //
        this.$refs.dialog_form.validate((validate) => {
          if (validate) {
            let id = this.form.id
            //id
            this.form.userId = sessionStorage.getItem('id')
            let url = this.url_request.ip_port_dev
            //
            if (id < 0) {
              //
              url += '/student_manage/add'
              this.addOrUpdateOrDelete(url, this.form)
            } else {
              //
              url += '/student_manage/update'
              this.addOrUpdateOrDelete(url, this.form)
            }
          } else {
            return false
          }
        })
      },
      //
      searchInfo () {
        let item = this.itemSelect
        let key = this.searchKeys.trim()
        const vm = this
        let condition = {
          currentPage: this.currentPage,
          count: this.count,
          userId: sessionStorage.getItem('id')
        }
        if (item === 'name') {
          condition.name = key
        } else if (item === 'number') {
          condition.number = key
        } else if (item === 'grade') {
          condition.grade = key
        } else if (item === 'department') {
          condition.department = key
        } else {
          condition.major = key
        }
        //
        let url = this.url_request.ip_port_dev + '/student_check'
        vm.netWorkRequest('post', url, condition, function (response) {
          //
          let pageInfo = response.pageInfo
          vm.totalPage = pageInfo.totalPage
          vm.totalCount = pageInfo.totalCount
          //
          vm.tableData = response.data
          vm.loading = false
        })
      },
      //
      reset () {
        const vm = this
        this.itemSelect = 'name'
        this.searchKeys = ''
        let url = this.url_request.ip_port_dev + '/student_check'
        vm.netWorkRequest('post', url, {
          userId: sessionStorage.getItem('id'),
          currentPage: 1,
          count: 100
        }, function (response) {
          //
          let pageInfo = response.pageInfo
          vm.totalPage = pageInfo.totalPage
          vm.totalCount = pageInfo.totalCount
          //
          vm.tableData = response.data
          vm.loading = false
        })
      },
      /**
       * 
       * @param url 
       * @param student 
       */
      addOrUpdateOrDelete (url, student) {
        const vm = this
        vm.netWorkRequest('post', url, student, function (response) {
          vm.$message({
            title: '',
            message: response,
            type: 'success',
            center: true
          })
          vm.reset()
          vm.dialogFormVisible = false
        })
      },
      //
      handleSizeChange (val) {
        this.count = val
        this.searchInfo()
      },
      //
      handleCurrentChange (val) {
        //
        this.currentPage = val
        this.searchInfo()
      },
      //,
      numberChange (value) {
        //
        if ((this.title === '') && (!isNaN(value))) {
          const vm = this
          const url = vm.url_request.ip_port_dev + '/public_data_student_info_query'
          vm.netWorkRequest('post', url, {number: value}, function (response) {
            //
            if (response !== '') {
              vm.form = response
              vm.form.id = -1
            }
          })
        }
      }
    },
    mounted () {
      this.reset()
    }
  }
</script>

<style scoped>
  .el-input {
    width: 200px;
  }

  .el-table {
    width: 100%;
    margin-top: 10px
  }
</style>



