<template>
  <!---->
  <div>
    <el-table :data="tableData" stripe border max-height="500" height="450"
              v-loading="loading" element-loading-text=""
              element-loading-spinner="el-icon-loading"
              element-loading-background="rgba(0, 0, 0, 0.8">
      <el-table-column align="center" label="" type="index" width="50"/>
      <el-table-column align="center" prop="homeWork.name" label=""/>
      <el-table-column align="center" prop="homeWork.describes" label=""/>
      <el-table-column align="center" prop="user.name" label=""/>
      <el-table-column align="center" label="">
        <template slot-scope="props">
          <span style="color:red;" v-if="props.row.submitState==='0'">
            
          </span>
          <span style="color:green;" v-else>
            
          </span>
        </template>
      </el-table-column>
      <el-table-column label="" align="center">
        <template slot-scope="scope">
          <el-upload
            class="upload-demo" ref="upload" :action="action" :file-list="file"
            :auto-upload="true" :data="form"
            :on-success="success" :on-error="error">
            <el-button @click="column_submit(scope.$index)" size="mini" type="primary">
              <i class="el-icon-upload"></i>
            </el-button>
          </el-upload>
          <el-button size="mini"  @click="column_download(scope.$index)">
            <i class="el-icon-download"></i> 
          </el-button>
        </template>
      </el-table-column>
    </el-table>
    <el-pagination background
                   @size-change="handleSizeChange"
                   @current-change="handleCurrentChange"
                   :current-page="currentPage" :page-sizes="counts"
                   :page-size="count"
                   layout="total, sizes, prev, pager, next, jumper"
                   :total="totalCount">
    </el-pagination>
  </div>
</template>

<script>
  export default {
    name: 'StudentTaskSubmit',
    data () {
      return {
        //
        file: [],
        //
        action: this.url_request.ip_port_dev + '/student_task_submit_upload',
        //
        form: {
          id: -1
        },
        tableData: [],
        //
        currentPage: 1,
        //
        totalPage: 1,
        counts:
          [100, 200, 300, 400, 500],
        //
        count: 100,
        //
        totalCount: 1,
        //
        itemSelect: 'name',
        searchKeys: '',
        loading: true
      }
    },
    methods: {
      //counts
      handleSizeChange (val) {
        this.count = val
        this.search()
      },
      //
      handleCurrentChange (val) {
        //
        this.currentPage = val
        this.search()
      },
      search () {
        const vm = this
        let url = vm.url_request.ip_port_dev + '/student_task_submit_check'
        vm.netWorkRequest('post', url, {
          username: sessionStorage.getItem('username'),
          currentPage: vm.currentPage,
          count: vm.count
        }, function (response) {
          //
          let pageInfo = response.pageInfo
          vm.totalPage = pageInfo.totalPage
          vm.totalCount = pageInfo.totalCount
          //
          vm.tableData = response.data
          vm.loading = false
        })
      },
      column_submit (index) {
        //id
        const id = this.tableData[index].id
        this.form.id = id
      },
      //
      success (response) {
        if (response === '') {
          this.$message({
            message: '',
            type: 'success'
          })
        } else {
          this.$message({
            message: '',
            type: 'error'
          })
        }
        this.file = []
        this.reset()
      },
      //
      error () {
        this.$message({
          message: '',
          type: 'error'
        })
      },
      column_download (index) {
        //
        const downloadUrl = this.tableData[index].homeWork.path
        const url = this.url_request.ip_port_dev + '/issue_task_download/'
        window.location.href = url + '?path=' + downloadUrl
      },
      //
      reset () {
        const vm = this
        let url = vm.url_request.ip_port_dev + '/student_task_submit_check'
        vm.netWorkRequest('post', url, {
          username: sessionStorage.getItem('username'),
          currentPage: 1,
          count: 100
        }, function (response) {
          //
          let pageInfo = response.pageInfo
          vm.totalPage = pageInfo.totalPage
          vm.totalCount = pageInfo.totalCount
          //
          vm.tableData = response.data
          vm.loading = false
        })
      }
    },
    mounted () {
      this.reset()
    }
  }
</script>

<style scoped>

</style>



