<template>
  <!---->
  <div>
    <el-button plain @click="exportExcel">
      <i class="el-icon-share"></i>excel
    </el-button>
    <!---->
    <el-select v-model="itemSelect" style="width: 120px">
      <el-option v-for="item in searchCondition" :key="item.value" :label="item.label"
                 :value="item.value">
      </el-option>
    </el-select>
    <el-input placeholder="" style="width: 180px"
              v-model="searchKeys" clearable/>
    <el-button icon="el-icon-search" plain @click="searchInfo"></el-button>
    <el-button icon="el-icon-refresh" plain @click="reset"></el-button>
    <!-- max-heightheight -->
    <el-table :data="tableData" stripe border max-height="500" height="450"
              v-loading="loading"
              element-loading-text=""
              element-loading-spinner="el-icon-loading"
              element-loading-background="rgba(0, 0, 0, 0.8"
              @select="selectionSingle"
              @select-all="selectAll">
      <el-table-column type="selection"/>
      <el-table-column label="" type="index" align="center" width="50"/>
      <el-table-column prop="name" label="" align="center"/>
      <el-table-column prop="grade" label="" align="center"/>
      <el-table-column prop="number" label="" align="center"/>
      <el-table-column prop="department" label="" align="center"/>
      <el-table-column prop="major" label="" align="center"/>
    </el-table>
    <el-pagination background
                   @size-change="handleSizeChange"
                   @current-change="handleCurrentChange"
                   :current-page="currentPage"
                   :page-sizes="counts"
                   :page-size="count"
                   layout="total, sizes, prev, pager, next, jumper"
                   :total="totalCount">
    </el-pagination>
  </div>
</template>

<script>
  export default {
    data () {
      return {
        //
        tableData: [],
        //
        currentPage: 1,
        //
        totalPage: 1,
        //
        counts: [100, 200, 300, 400, 500],
        //
        count: 100,
        //
        totalCount: 1,
        //
        itemSelect: 'name',
        searchKeys: null,
        //id
        selections: [],
        searchCondition: [
          {value: 'name', label: ''},
          {value: 'number', label: ''},
          {value: 'grade', label: ''},
          {value: 'department', label: ''},
          {value: 'major', label: ''},
        ],
        loading: true
      }
    },
    methods: {
      //
      handleSizeChange (val) {
        this.count = val
        this.searchInfo()
      },
      //
      handleCurrentChange (val) {
        // 
        this.currentPage = val
        this.searchInfo()
      },
      //
      searchInfo () {
        let item = this.itemSelect
        let key = this.searchKeys.trim()
        const vm = this
        let condition = {
          currentPage: this.currentPage,
          count: this.count,
          userId: sessionStorage.getItem('id')
        }
        if (item === 'name') {
          condition.name = key
        } else if (item === 'number') {
          condition.number = key
        } else if (item === 'grade') {
          condition.grade = key
        }
        else if (item === 'department') {
          condition.department = key
        } else {
          condition.major = key
        }
        //
        let url = this.url_request.ip_port_dev + '/student_check'
        vm.netWorkRequest('post', url, condition, function (response) {
          //
          let pageInfo = response.pageInfo
          vm.totalPage = pageInfo.totalPage
          vm.totalCount = pageInfo.totalCount
          //
          vm.tableData = response.data
          vm.loading = false
        })
      },
      //
      reset () {
        const vm = this
        this.itemSelect = 'name'
        this.searchKeys = ''
        let url = this.url_request.ip_port_dev + '/student_check'
        vm.netWorkRequest('post', url, {
          userId: sessionStorage.getItem('id'),
          currentPage: 1,
          count: 100
        }, function (response) {
          //
          let pageInfo = response.pageInfo
          vm.totalPage = pageInfo.totalPage
          vm.totalCount = pageInfo.totalCount
          //
          vm.tableData = response.data
          vm.loading = false
        })
      },
      //selection
      //selection
      selectionSingle (selection, row) {
        this.selections = selection
      },
      //
      selectAll (selection) {
        this.selections = selection
      },
      //excel
      exportExcel () {
        //id
        const ids = []
        if (this.selections.length === 0) {
          this.$message({
            message: '!',
            type: 'warning'
          })
          return
        }
        this.selections.forEach(item => {
          ids.push(item.id)
        })
        let url = this.url_request.ip_port_dev + '/student_export'
        //,axios
        window.location.href = url + '?ids=' + ids
      }
    },
    //
    mounted () {
      this.reset()
    }
  }
</script>

<style scoped>

</style>



