<template>
  <div>
    <el-form :model="form" :rules="rules" ref="login_form" label-width="100px"
             class="login-form">
      <p></p>
      <el-form-item label=":" prop="username">
        <el-input v-model="form.username" clearable
                  prefix-icon="my-icon-user" placeholder=""/>
      </el-form-item>
      <el-form-item class="my-font-color-white" label=":" prop="password">
        <el-input v-model="form.password" show-password
                  prefix-icon="my-icon-password"
                  @keyup.enter.native="login" placeholder=""></el-input>
      </el-form-item>
      <el-form-item>
        <el-button type="primary" @click="login"></el-button>
        <el-button @click="reset"></el-button>
        <el-tooltip class="item" effect="dark" content="" placement="top-start">
          <el-button></el-button>
        </el-tooltip>
      </el-form-item>
      <el-form-item>
        <router-link class="register" to="/register"></router-link>
      </el-form-item>
    </el-form>
  </div>
</template>
<script>
  export default {
    name: 'Login',
    data () {
      return {
        form: {
          username: '',
          password: ''
        },
        info_form: {
          username: '',
          number: '',
          mobile: '',
          email: ''
        },
        visible: false,
        rules: {
          username: [
            {required: true, message: '', trigger: 'blur'},
            {min: 6, max: 20, message: '6-20', trigger: 'blur'}
          ],
          password: [
            {required: true, message: '', trigger: 'blur'},
            {min: 6, max: 20, message: '6-20', trigger: 'blur'}
          ]
        },
        dialogFormVisible: false
      }
    },
    methods: {
      login () {
        const vm = this
        const username = this.form.username
        const password = this.form.password
        this.$refs.login_form.validate((valid) => {
          if (valid) {
            this.netWorkRequest('post', vm.url_request.ip_port_dev + '/login',
              {username: username, password: password},
              function (response) {
                let result = response.message
                let role = response.role
                let name = response.name
                let id = response.id
                if (result === vm.login_show.success) {
                  //,,
                  sessionStorage.setItem('id', id)
                  sessionStorage.setItem('username', username)
                  sessionStorage.setItem('name', name)
                  sessionStorage.setItem('role', role)
                  //
                  if (role === '0') {
                    vm.$router.replace('/main/studentTaskSubmit')
                  } else {
                    vm.$router.replace('/main')
                  }
                } else {
                  vm.$message({
                    title: '',
                    message: result,
                    type: 'error',
                    center: true
                  })
                }
              }
            )
          } else {
            return false
          }
        })
      },
      reset () {
        this.$refs.login_form.resetFields()
      },
      //
      ownRetrieve () {
        this.dialogFormVisible = true
      },
      //,
      personRetrieve () {
        this.dialogFormVisible = true
      }
    }
  }
</script>

<style scoped>
  .login-form {
    width: 50%;
    text-align: center;
    margin: 140px auto;
  }

  .el-input {
    width: 80%;
  }

  .register {
    text-decoration: none;
    font-size: large;
    color: white;
  }

  .toast {
    color: white;
  }

  p {
    color: white;
    font-size: 30px;
    margin-left: 80px;
    letter-spacing: 5px;
    font-weight: bold;
  }

</style>




