<template>
  <el-form :model="form" :rules="rules" ref="login_form" label-width="100px"
           class="login-form">
    <p></p>
    <el-form-item label=":" prop="username">
      <el-input v-model="form.username" clearable
                @change="usernameChange" placeholder=""></el-input>
    </el-form-item>
    <el-form-item label=":" prop="number">
      <el-input v-model="form.number" clearable
                placeholder=""></el-input>
    </el-form-item>
    <el-form-item label=":" prop="password">
      <el-input v-model="form.password" show-password
                @keyup.enter.native="login" placeholder=""></el-input>
    </el-form-item>
    <el-form-item label=":" prop="again">
      <el-input v-model="form.again" show-password
                placeholder=""></el-input>
    </el-form-item>
    <el-form-item label=":" prop="name">
      <el-input v-model="form.name"
                placeholder=""></el-input>
    </el-form-item>
    <el-form-item>
      <el-button type="primary" @click="register"></el-button>
      <el-button @click="reset"></el-button>
    </el-form-item>
    <el-form-item>
      <router-link class="back" to="/login"></router-link>
    </el-form-item>
  </el-form>
</template>
<script>
  export default {
    name: 'Register',
    data () {
      return {
        form: {
          username: null,
          password: null,
          again: null,
          name: null,
          number: null
        },
        rules: {
          username: [
            {required: true, message: '', trigger: 'blur'},
            {min: 6, max: 20, message: '6-20', trigger: 'blur'}
          ],
          password: [
            {required: true, message: '', trigger: 'blur'},
            {min: 6, max: 20, message: '6-20', trigger: 'blur'}
          ],
          number: [
            {required: true, message: '', trigger: 'blur'},
            {min: 6, max: 11, message: '6-11', trigger: 'blur'}
          ],
          again: [
            {required: true, message: '', trigger: 'blur'},
            {min: 6, max: 20, message: '6-20', trigger: 'blur'}
          ],
          name: [
            {required: true, message: '', trigger: 'blur'},
            {min: 2, max: 6, message: '2-6', trigger: 'blur'}
          ]
        }
      }
    },
    methods: {
      //
      usernameChange (value) {
        const vm = this
        const url = vm.url_request.ip_port_dev + '/public_data_query_user'
        if (value.trim().length >= 6) {
          vm.netWorkRequest('get', url, {
            username: value
          }, function (response) {
            if (response !== '') {
              vm.$message.error(response)
            }
          })
        }
      },
      register () {
        const vm = this
        let password = vm.form.password
        let again = vm.form.again
        const url = vm.url_request.ip_port_dev + '/register'
        vm.$refs.login_form.validate((valid) => {
          if (valid) {
            if (password !== again) {
              vm.$message.error(',')
              return false
            }
            //
            vm.netWorkRequest('post', url, vm.form, function (response) {
              vm.$message(response)
            })
          }
        })
      },
      reset () {
        this.$refs.login_form.resetFields()
      }
    }
  }
</script>

<style scoped>
  .login-form {
    width: 50%;
    text-align: center;
    margin: 110px auto;
  }

  .el-input {
    width: 80%;
  }

  .back {
    text-decoration: none;
    font-size: large;
    margin-left: 200px;
    color: white;
  }

  p {
    color: white;
    font-size: 30px;
    margin-left: 80px;
    letter-spacing: 5px;
    font-weight: bold;
  }
</style>



