<template>
  <!---->
  <div>
    <el-table :data="tableData" stripe border max-height="500" height="450"
              v-loading="loading" element-loading-text=""
              element-loading-spinner="el-icon-loading"
              element-loading-background="rgba(0, 0, 0, 0.8">
      <el-table-column align="center" label="" type="index" width="50"/>
      <el-table-column align="center" prop="homeWork.name" label=""/>
      <el-table-column align="center" prop="user.name" label=""/>
      <el-table-column align="center" label="">
        <template slot-scope="props">
          <span style="color:red;" v-if="props.row.submitState==='0'">
            
          </span>
          <span style="color:green;" v-else>
            
          </span>
        </template>
      </el-table-column>
      <el-table-column type="expand" align="center" width="50px" label="">
        <template slot-scope="props">
          <p>:{{ props.row.score }}</p>
          <p>:{{ props.row.remark }}</p>
        </template>
      </el-table-column>
    </el-table>
    <el-pagination background
                   @size-change="handleSizeChange" @current-change="handleCurrentChange"
                   :current-page="currentPage" :page-sizes="counts"
                   :page-size="count"
                   layout="total, sizes, prev, pager, next, jumper" :total="totalCount">
    </el-pagination>
  </div>
</template>

<script>
  export default {
    name: 'StudentTaskScoreCheck',
    data () {
      return {
        tableData: [],
        currentPage: 1,
        totalPage: 1,
        counts: [100, 200, 300, 400, 500],
        count: 100,
        totalCount: 1,
        itemSelect: 'name',
        searchKeys: '',
        form: {
          id: -1
        },
        loading: false
      }
    },
    methods: {
      reset () {
        const vm = this
        const url = vm.url_request.ip_port_dev + '/student_task_submit_score'
        vm.netWorkRequest('post', url, {
          username: sessionStorage.getItem('username'),
          currentPage: 1,
          count: 100
        }, function (response) {
          //
          let pageInfo = response.pageInfo
          vm.totalPage = pageInfo.totalPage
          vm.totalCount = pageInfo.totalCount
          //
          vm.tableData = response.data
          vm.loading = false
        })
      },
      //counts
      handleSizeChange (val) {
        this.count = val
        this.search()
      },
      //
      handleCurrentChange (val) {
        //
        this.currentPage = val
        this.search()
      },
    },
    mounted () {
      this.reset()
    }
  }
</script>

<style scoped>
  p {
    color: red;
  }
</style>



