<template>
  <!--/-->
  <div style="text-align: center;font-weight: bold;color: white">
    <p></p>
    <span v-if="role==='0'">
     <el-form :model="form_student" :rules="rules" class="alter-info-form"
              ref="form_student">
       <el-form-item label=" :" prop="grade">
        <el-input v-model="form_student.grade"
                  placeholder=""></el-input>
        </el-form-item>
       <el-form-item label=" :" prop="department">
        <el-input v-model="form_student.department"
                  placeholder=""></el-input>
        </el-form-item>
       <el-form-item label=" :" prop="major">
        <el-input v-model="form_student.major"
                  placeholder=""></el-input>
        </el-form-item>
       <el-form-item label=":" prop="classTeacher">
        <el-input v-model="form_student.classTeacher"
                  placeholder=""></el-input>
        </el-form-item>
        <el-button @click="reset"></el-button>
        <el-button type="primary" @click="ensure"> </el-button>
     </el-form>
     </span>
    <span v-if="role==='1'">
     
     </span>
    <span v-if="role==='2'">
      
     </span>
  </div>
</template>

<script>
  export default {
    name: 'PersonInfoAlter',
    data () {
      return {
        //
        role: null,
        form_student: {
          id: -1,//id
          grade: null,
          department: null,
          major: null,
          classTeacher: null
        },
        rules: {
          grade: [
            {required: true, message: ''},
            {min: 6, max: 20, message: '6-20'}
          ],
          number: [
            {required: true, message: ''},
            {type: 'number', message: ''}
          ],
          department: [
            {required: true, message: ''},
            {min: 3, max: 20, message: '3-20'}
          ],
          major: [
            {required: true, message: ''},
            {min: 3, max: 20, message: '3-20'}
          ],
          classTeacher: [
            {required: true, message: ''},
            {min: 2, max: 6, message: '2-6'}
          ]
        },
        form_teacher: {}
        ,
        form_admin: {}
      }
    },
    methods: {
      reset () {
        this.$refs.form_student.resetFields()
      },
      ensure () {
        this.form_student.id = sessionStorage.getItem('id')
        this.form_student.username = sessionStorage.getItem('username')
        const vm = this
        const url = vm.url_request.ip_port_dev + '/user_update_student'
        this.$refs.form_student.validate((valid) => {
          if (valid) {
            vm.netWorkRequest('post', url, vm.form_student, function (response) {
              vm.$refs.form_student.resetFields()
              vm.$message(response)
            })
          }
        })
      }
    },
    //
    mounted () {
      const role = sessionStorage.getItem('role')
      this.role = role
      const username = sessionStorage.getItem('username')
      //
      const vm = this
      const url = vm.url_request.ip_port_dev + '/getUserInfoByUsername'
      vm.netWorkRequest('post', url, {
        username: username
      }, function (response) {
        vm.form_student = response
        vm.form_teacher = response
        vm.form_admin = response
      })
    }
  }
</script>

<style scoped>
  .alter-info-form {
    width: 40%;
    text-align: center;
    margin: 50px auto;
    padding: 10px;
    border: blanchedalmond solid 2px;
  }

  .el-input {
    width: 80%;
  }

  .title {
    color: white;
    font-size: 30px;
  }
</style>



