<template>
  <!--,-->
  <div>
    <div class="header">
      <el-button plain class="el-icon-user-solid" @click="addUser"></el-button>
      <el-select v-model="itemSelect" style="width: 120px">
        <el-option v-for="item in searchCondition" :key="item.value" :label="item.label"
                   :value="item.value">
        </el-option>
      </el-select>
      <el-input v-model="searchKeys" placeholder="" style="width: 200px"></el-input>
      <el-button icon="el-icon-search" plain @click="search"></el-button>
      <el-button icon="el-icon-refresh" plain @click="reset"></el-button>
    </div>
    <!-- max-heightheight -->
    <el-table :data="tableData" stripe border max-height="500" height="450"
              v-loading="loading" element-loading-text=""
              element-loading-spinner="el-icon-loading"
              element-loading-background="rgba(0, 0, 0, 0.8">
      <el-table-column align="center" label="" type="index" width="50"/>
      <el-table-column align="center" prop="name" sortable label=""/>
      <el-table-column align="center" prop="username" sortable label=""/>
      <el-table-column align="center" prop="createDate" sortable label=""/>
      <el-table-column align="center" label="" width="80">
        <template slot-scope="props">
          <span v-if="props.row.state==='1'" style="color: green">
            
          </span>
          <span v-else style="color: red">
          
        </span>
        </template>
      </el-table-column>
      <el-table-column label="" align="center" width="260px">
        <template slot-scope="scope">
          <span v-if="scope.row.state==='0'">
         <el-button size="mini" class="el-icon-unlock"
                    type="success" @click="column_freezeOrThaw(scope.$index,1)">
          </el-button>
          </span>
          <span v-else>
            <el-button size="mini"
                       class="el-icon-lock" type="danger" @click="column_freezeOrThaw(scope.$index,0)"></el-button>
          </span>
          <el-button size="mini" type="primary" class="el-icon-edit-outline"
                     @click="column_edit(scope.$index)">
          </el-button>
          <el-button class="el-icon-delete-solid" size="mini" type="danger"
                     @click="column_delete(scope.$index)">
          </el-button>
        </template>
      </el-table-column>
    </el-table>
    <el-pagination background
                   @size-change="handleSizeChange"
                   @current-change="handleCurrentChange"
                   :current-page="currentPage" :page-sizes="counts"
                   :page-size="count"
                   layout="total, sizes, prev, pager, next, jumper"
                   :total="totalCount">
    </el-pagination>
    <!---->
    <el-dialog :title="title" :visible.sync="dialogFormVisible"
               :center="true" :close-on-click-modal="false">
      <el-form :model="form" :rules="rules" ref="dialog_form" label-width="100px">
        <el-form-item label=":" prop="username">
          <el-input @change="usernameChange" v-model="form.username"/>
        </el-form-item>
        <el-form-item label=":" prop="name">
          <el-input v-model="form.name"/>
        </el-form-item>
        <el-form-item label=":" prop="role">
          <el-radio v-model="form.role" label="0"></el-radio>
          <el-radio v-model="form.role" label="1"></el-radio>
          <el-radio v-model="form.role" label="2"></el-radio>
        </el-form-item>
        <el-form-item>
          <p v-if="title===''" style="color: red;font-size: large">
            ,
          </p>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="dialogFormVisible = false"> </el-button>
        <el-button type="primary" @click="ok"> </el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
  export default {
    name: 'UserManage',
    data () {
      return {
        loading: true,
        title: '',
        searchCondition: [
          {value: 'name', label: ''},
          {value: 'username', label: ''}
        ],
        dialogFormVisible: false,
        form: {
          id: -1,
          username: null,
          name: null,
          role: '0',
        },
        rules: {
          username: [
            {required: true, message: '', trigger: 'blur'},
            {min: 6, max: 20, message: '6-20', trigger: 'blur'}
          ],
          name: [
            {required: true, message: '', trigger: 'blur'},
            {min: 2, max: 20, message: '2-6', trigger: 'blur'}
          ],
          role: [
            {required: true},
          ]
        },
        tableData: [],
        //
        currentPage: 1,
        //
        totalPage: 1,
        counts:
          [100, 200, 300, 400, 500],
        //
        count: 100,
        //
        totalCount: 1,
        //
        itemSelect: 'name',
        searchKeys: '',
      }
    },
    methods: {
      //counts
      handleSizeChange (val) {
        this.count = val
        this.search()
      },
      //
      handleCurrentChange (val) {
        //
        this.currentPage = val
        this.search()
      },
      addUser () {
        this.title = ''
        this.form.id = -1
        this.form.name = ''
        this.form.username = ''
        this.form.role = '0'
        this.dialogFormVisible = true
      },
      //
      column_edit (index) {
        this.title = ''
        //
        let obj = Object.assign({}, this.tableData[index])
        this.form = obj
        this.dialogFormVisible = true
      },
      //
      column_delete (index) {
        const vm = this
        const id = vm.tableData[index].id
        const url = vm.url_request.ip_port_dev + '/user_manage_delete'
        vm.myConfirm(null, null, function () {
          vm.netWorkRequest('post', url, {
            id: id
          }, function (response) {
            vm.$message(response)
            vm.reset()
          })
        })
      },
      /**
       * 
       * @param index 
       * @param what 10
       */
      column_freezeOrThaw (index, what) {
        const vm = this
        const url = vm.url_request.ip_port_dev + '/user_manage_freezeOrThaw'
        const id = vm.tableData[index].id
        let state = '0'
        if (what === 1) {
          state = '1'
        }
        vm.netWorkRequest('post', url, {
          id: id,
          state: state
        }, function (response) {
          vm.$message(response)
          vm.reset()
        })
      },
      //
      search () {
        const vm = this
        vm.loading = true
        const condition = {
          currentPage: vm.currentPage,
          count: vm.count
        }
        if (vm.itemSelect === 'name') {
          condition.name = vm.searchKeys
        } else {
          condition.username = vm.searchKeys
        }
        let url = vm.url_request.ip_port_dev + '/user_manage_query'
        vm.netWorkRequest('post', url, condition, function (response) {
            //,json
            vm.tableData = response.data
            //,json
            let pageInfo = response.pageInfo
            //
            vm.totalCount = pageInfo.totalCount
            vm.totalPage = pageInfo.totalPage
            vm.loading = false
          }
        )
      },
      //
      reset () {
        const vm = this
        let url = vm.url_request.ip_port_dev + '/user_manage_query'
        vm.currentPage = 1
        vm.netWorkRequest('post', url, {
          currentPage: 1,
          count: 100
        }, function (response) {
          //,json
          vm.tableData = response.data
          //,json
          let pageInfo = response.pageInfo
          //
          vm.totalCount = pageInfo.totalCount
          vm.totalPage = pageInfo.totalPage
          vm.loading = false
        })
      }
      ,
      ok () {
        const vm = this
        const url = vm.url_request.ip_port_dev + '/user_manage_addOrUpdate'
        vm.$refs.dialog_form.validate(valid => {
          if (valid) {
            vm.netWorkRequest('post', url, vm.form, function (response) {
              vm.$message(response)
              vm.reset()
              vm.dialogFormVisible = false
            })
          }
        })
      }
      ,
      //,
      usernameChange (value) {
        const vm = this
        const url = vm.url_request.ip_port_dev + '/public_data_query_user'
        if (value.trim().length >= 6) {
          vm.netWorkRequest('get', url, {
            username: value
          }, function (response) {
            if (response !== '') {
              vm.$message.error(response)
            }
          })
        }
      }
    },
    mounted () {
      this.reset()
    }
  }
</script>

<style scoped>
  .header {
    text-align: left;
  }

</style>



